* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.page {
  min-height: 100vh;
}

.page .content-area {
  min-height: 100vh;
  transition: .3s;
  will-change: width;
  float: right;
  background-color: #f2f1f6;
}

.page .content-area.no-sidebar {
  width: 100% !important;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #2c3e50;
  padding: 15px;
  transition: .3s;
  color: #fff;
}

.page .sidebar.no-sidebar {
  left: -250px;
}

.page .sidebar .logo-area {
  font-weight: bold;
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.page .sidebar .links-area li a {
  color: #ccc;
  text-decoration: none;
  padding: 10px;
  display: block;
  position: relative;
  padding: 10px 20px;
  transition: .3s;
}

.page .sidebar .links-area > li > a {
  position: relative;
  color: #fff;
}

.page .sidebar .links-area > li > a:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid #fff;
  transform: translateY(-50%);
}

.page .sidebar .links-area > li > a:hover {
  padding-left: 30px;
}

.page .sidebar .links-area .toggle-submenu {
  float: right;
  padding-top: 5px;
}

.page .sidebar .links-area .child-links {
  position: relative;
  display: none;
}

.page .sidebar .links-area .child-links:before {
  content: '';
  position: absolute;
  left: 3px;
  top: 0;
  width: 3px;
  height: 100%;
  background-color: #666;
}

.page .sidebar .links-area .child-links a {
  position: relative;
}

.page .sidebar .links-area .child-links a:hover:before {
  content: '';
  position: absolute;
  top: 0;
  left: 3px;
  width: 3px;
  height: 100%;
  background-color: #fff;
}

.header {
  background-color: #fff;
  padding: 20px;
}

.header .toggle-sidebar {
  cursor: pointer;
}

.header .notifaction-contaienr {
  display: inline-block;
  margin-left: 20px;
}

.header .notifaction-contaienr .toggle-notfiactions {
  background: #eee;
  border-radius: 50%;
  padding: 10px;
  color: #555;
  cursor: pointer;
}

.header .notifaction-contaienr .dropdown-menu {
  min-width: 600px;
  border: none;
  box-shadow: 10px 10px 10px #ddd;
  position: relative;
  top: 10px !important;
}

.header .notifaction-contaienr .dropdown-menu:before {
  content: '';
  position: absolute;
  top: -22px;
  left: 4px;
  border-style: solid;
  border-width: 15px;
  border-color: transparent transparent #333 transparent;
}

.header .notifaction-contaienr .dropdown-menu .noti-head {
  background-color: #333;
  padding: 15px;
  color: #fff;
}

.header .notifaction-contaienr .dropdown-menu .noti-head span {
  float: right;
}

.header .notifaction-contaienr .dropdown-menu .noti-contnet {
  padding: 20px;
}

.header .notifaction-contaienr .dropdown-menu .noti-contnet ul {
  margin: 0;
}

.header .notifaction-contaienr .dropdown-menu .noti-contnet ul li {
  padding: 10px;
  font-size: 1.2rem;
}

.header .notifaction-contaienr .dropdown-menu .noti-contnet ul li i {
  background-color: #eee;
  padding: 10px;
  margin-right: 20px;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 30px;
  color: #2c3e50;
}

.header .notifaction-contaienr .dropdown-menu .noti-contnet ul:last-child {
  padding-bottom: 0;
}

.header .notifaction-contaienr .noti-footer a {
  display: block;
  padding: 10px;
  border-top: 2px solid #eee;
  text-decoration: none;
}
